import axios from 'axios'
const baseUrl = 'api/list'

const getAllTemp = () => {
    const request = axios.get(baseUrl)
    return request.then(response => response.data)
}

const searchTemp = () => {
    const request = axios.get(`${baseUrl}/searchResult/`)
    return request.then(response => response.data)
}

export default{ getAllTemp, searchTemp}